<template>
  <div>
        <el-form ref="addForm" :rules="rules" :model="dataForm" label-width="150px">
            <el-row>
                <el-col :span="22">
                    <el-form-item label="元数据名称" prop="tabCode">
                        <el-input type="text" v-model="dataForm.tabCode" maxlength='15' placeholder="请输入元数据名称"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="名称" prop="tabName">
                        <el-input type="text" v-model="dataForm.tabName" maxlength='10' placeholder="请输入名称"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="类型" prop="tabType">
                        <el-radio-group v-model="dataForm.tabType">
                            <el-radio :label="1">表</el-radio>
                            <el-radio :label="2">视图</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="是否启用" prop="tabStatus">
                        <el-radio-group v-model="dataForm.tabStatus">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="多记录信息集" prop="tabIsmultily">
                        <el-radio-group v-model="dataForm.tabIsmultily">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="可用于自定义查询" prop="tabIsquery">
                        <el-radio-group v-model="dataForm.tabIsquery">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="描述" prop="tabDesc">
                        <el-input type="text" v-model="dataForm.tabDesc" placeholder="请输入菜单描述"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <div class="dialog-footer">
                <el-button type="primary" @click="saveArea()">保存</el-button>
                <el-button type="danger" @click="cancle()">取消</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default{
        props: [
                'checkedData',
                'edikGetDataList',
                'isEdit'//true：添加，false 编辑
        ],
        data(){
            return{
                //表单验证
                rules: {
                    tabCode: [{
                        required: true,
                        message: '请输入元数据名称',
                        trigger: 'change'
                    }],
                    tabName: [{
                        required: true,
                        message: '请输入名称',
                        trigger: 'change'
                    }],
                },
            
                dataForm:{
                    tabId:'',
                    typeId:'',
                    tabCode:'',
                    tabName:'',
                    tabType:1,
                    tabStatus:1,
                    tabIsmultily:1,
                    tabIsquery:1,
                    tabDesc:''
                },
               
            }
        },
        created(){ 
            this.setFormData()
        },
        methods:{
            setFormData(){
                if(this.isEdit === false){
                    this.dataForm.tabId = this.checkedData.id
                    this.dataForm.tabCode = this.edikGetDataList.tabCode
                    this.dataForm.tabName = this.edikGetDataList.tabName
                    this.dataForm.tabType = this.edikGetDataList.tabType
                    this.dataForm.tabStatus = this.edikGetDataList.tabStatus
                    this.dataForm.tabIsmultily = this.edikGetDataList.tabIsmultily
                    this.dataForm.property = this.edikGetDataList.property
                    this.dataForm.tabDesc = this.edikGetDataList.tabDesc
                }else{
                    this.dataForm.typeId = this.checkedData.id
                }
            },

            //保存
            saveArea(){
                this.$refs['addForm'].validate((valid) => {
                    if(valid) {
                        this.$http({
                            url: this.$http.adornUrl(this.API.savaOrUpdateInfo),
                            method: 'post',
                            data: this.$http.adornData(
                                this.dataForm
                            )
                        }).then((res) =>{
                            var resData = res.data;
                            if(resData.code == 0) {
                                this.$message({
                                    message: '成功',
                                    type: 'success'
                                });
                                this.$emit("addback",true);
                            } else {
                                this.$message.error(resData.msg);
                            }
                        })
                    } 
                })
            },
            //取消
            cancle() {
                this.$emit("addback",false);
            } 
        }
    }
</script>

<style>
</style>



